import { Component } from "@angular/core";
@Component({
    templateUrl: './word.component.html',
    selector: 'app-word',
    styleUrls: ['./word.component.css']
})

export class Wordcomponent {
    en: string='Hello';
    vn='Xin chao';
    imagesURL='https://angular.io/assets/images/logos/angular/logo-nav@2x.png';
    forgot = false;  // đã quên (hiển thị hay không)
    toggleForgot(){
        this.forgot= !this.forgot;
    }
}